<template>
    <div style="width: 500px;">
        <el-row>
            <el-col :span="10">
                <el-select v-loading="loading" size="mini" :value="pCatId" class="selectDestination" filterable
                           placeholder="请选择" @change="setPCatId" >
                    <el-option label="请选择" :value="0"></el-option>
                    <el-option v-for="item in datas" :label="item.name" :value="item.cat_id"></el-option>
                </el-select>
            </el-col>
            <el-col :span="2" style="text-align: center"><i class="el-icon-minus"></i></el-col>
            <el-col :span="10">
                <el-select v-loading="loading" size="mini" :value="catId" class="selectDestination" filterable placeholder="请选择"
                           @change="setCatId" >
                    <el-option :key="catId" label="请选择" :value="0"></el-option>
                    <el-option   v-for="item in getChild(pCatId)" :label="item.name" :value="item.cat_id"></el-option>
                </el-select>
            </el-col>
            <el-col :span="2"  v-if="!(noAdd === true)">
                <el-button v-if="dialog == false" @click="dialog=true" type="text">+添加商品分类</el-button>
            </el-col>
        </el-row>

        <!--编辑新增 弹窗-->
        <el-dialog title="分类" v-loading="addCategoryLoading" :visible.sync="dialog" width="50%"
                   :modal-append-to-body="false">
            <el-form :model="form" style="padding: 0" size="small">
                <el-form-item label="分类名称" label-width="80px">
                    <el-input v-model="form.name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="上级分类" label-width="80px">
                    <el-select v-model="form.parent_id" placeholder="请选择">
                        <el-option :value="0" label="顶级分类">顶级分类</el-option>
                        <el-option v-for="item in datas" :value=item.cat_id :label="item.name"></el-option>
                    </el-select>
                    <p class="tit"> 1)创建一级分类选择顶级分类</p>
                    <p class="tit"> 2)创建二级分类选择要创建分类的上级分类</p>
                </el-form-item>
                <el-form-item label="是否推荐" label-width="80px">
                    <el-switch v-model="form.is_hot" :value="form.is_hot" :active-value="1"
                               :inactive-value="0"></el-switch>
                </el-form-item>

                <el-form-item label="是否显示" label-width="80px">
                    <el-switch v-model="form.is_show" :value="form.is_show" :active-value="1"
                               :inactive-value="0"></el-switch>
                </el-form-item>

                <el-form-item label="排序" label-width="80px">
                    <el-input v-model="form.orderby" autocomplete="off"></el-input>
                </el-form-item>

                <el-form-item label="封面图片" label-width="80px">
                    <wyu-upload :multi="false" :img="form.image" v-model="form.image"></wyu-upload>
                    <p class="tit"> 1)一级分类建议尺寸265px*90px或等比缩放</p>
                    <p class="tit"> 2)二级分类建议尺寸81px*81px或等比缩放</p>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button size="small" @click="dialog = false">取 消</el-button>
                <el-button size="small" type="primary" @click="addCat()">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
    import * as api from '@/api.js';
    import WyuUpload from "@/components/Photos";
    export default {
        name: 'ProductCategory',
        components: {WyuUpload},
        props: ['pCatId','catId','noAdd'], //
        data() {
            return {
                datas: [],
                dialog: false,
                addCategoryLoading: false,
                name: '',
                p_cat_id: 0,
                isAdd: 0,
                category: [],
                loadingbtn: false,
                value: [],
                form: {
                    name: '',
                    parent_id: 0,
                    orderby: 0,
                    is_show: 0,
                    is_hot: 0,
                    image: '',
                },
            }
        },
        created() {
            this.getDatas();
        },
        methods: {
            getChild(pCatId){
                let Child = [];
                this.datas.some(v => {
                    if (v.cat_id == pCatId) {
                        Child = v.child;
                        return false;
                    }
                });
                return Child;
            },

            setPCatId(val) {
                this.$emit("selCat",{
                    cat_id:0,
                    p_cat_id:val,
                });
            },
            setCatId(val){
                this.$emit("selCat",{
                    cat_id:val,
                    p_cat_id:this.pCatId,
                });
            },
            addCat() {
                if (this.form.name == '') return false;
                if (this.form.parent_id < 0) return false;
                if (this.form.image == '') return false;
                this.loading = true;
                api.post('shop/admin.goods.category/categoryCreate', {
                    appToken: this.$store.getters.appToken,
                    name: this.form.name,
                    parent_id: this.form.parent_id,
                    orderby: this.form.orderby,
                    is_show: this.form.is_show,
                    is_hot: this.form.is_hot,
                    image: this.form.image,
                }).then(res => {
                    this.loading = false;
                    this.dialog = false;
                    this.name = "";
                    this.parent_id = 0;
                    this.orderby = 0;
                    this.is_show = 0;
                    this.is_hot = 0;
                    this.image = '';
                    this.getDatas();
                }).catch((error) => {
                    this.loading = false;
                });
            },

            getDatas() {
                var that = this;
                that.loading = true;
                api.post('/shop/admin.goods.category/categoryList', {
                    appToken: this.$store.getters.appToken,
                }).then(res => {
                    this.datas = res.list;
                    that.loading = false;
                }).catch((error) => {
                    that.loading = false;
                });
            },
        },
    }
</script>

<style scoped>
    .selectDestination {
        float: left;
        width: 100%;
    }
</style>
